const Slider = ({ props }) => {
// console.log(this.props)
   return <Carousel 
        interval="4000"
        className={Styles.carousel}>
        <Carousel.Item className={Styles.item}>
            <Image1/>
            <Carousel.Caption className={Styles.holder_akcija}>
              <div className={Styles.wraper_akcija}>
              <h3>&#8222;Generalka&#8221;</h3>
              <p classname={Styles.akcija}><strong>Akcija</strong> paket usluga</p>
              <p>Iskoristite letnji popust</p>
              <Link to="/akcije">Saznaj više</Link>
              </div>
            </Carousel.Caption>
        </Carousel.Item>

        <Carousel.Item className={Styles.item}>
            <Image1/>
            <Carousel.Caption className={Styles.holder_akcija}>
              <div className={Styles.wraper_akcija}>
              <h3>&ldquo;Generalka&rdquo;</h3>
              <p className={Styles.akcija}><strong><span>Akcija</span></strong>-Paket usluga</p>
              <p>Iskoristite letnji popust</p>
              <Link to="/akcije">Saznaj više</Link>
              </div>
            </Carousel.Caption>
        </Carousel.Item>

        <Carousel.Item className={Styles.item}>
            <Image1/>
            <Carousel.Caption className={Styles.holder_akcija}>
              <div className={Styles.wraper_akcija}>
              <h3>"Generalka"</h3>
              <p classname={Styles.akcija}><strong>Akcija</strong> paket usluga</p>
              <p>Iskoristite letnji popust</p>
              <Link to="/akcije">Saznaj više</Link>
              </div>
            </Carousel.Caption>
        </Carousel.Item>
   
    </Carousel>

}

export default Slider;

